.newCompany{
    display: flex;
    width: 100%;

    .newCompanyContainer{
        flex: 6;

        .top, .bottom{
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47); 
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            padding: 10px;
            margin: 20px;
            display: flex;

            .titleHading{
                color: lightgray;
                font-size: 20px;
            }
        }
    }
}